@import "variables";
@import "mixin";

html{
    font-size: 100%;
    box-sizing: border-box;
}

*{
box-sizing: inherit;
}
body{
    margin: 0;
    padding: 0;
    font-family: 'Public Sans', sans-serif;
    font-size:$font-med;
    font-weight: 300;
    color: $grayishBlue;
    line-height: 1.3;
    overflow-x: hidden;

    @include breakpoint-up(large){
        font-size: $font-med;
    }

    &.noscroll{
        overflow: hidden;
    }

    // min-height: 70vh;
}

h1,h2,h3{
    margin-top: 0;
    font-weight: 300;
    line-height: 1.5;
    color: $darkBlue;
}

h1{
    font-size: 2.31rem;
    margin-bottom: 1.5rem;

    @include breakpoint-up(large){
    font-size: $font-xlg;
    }
}


h2{
    font-size: 1.875rem;
    margin-bottom: 1.5625rem;


    @include breakpoint-up(large){

        font-size: 2.25rem;
        margin-bottom: 2.25rem;
    }
}

p{
    line-height: 1.5;
    margin-bottom: 2.25rem;
}

a,a:visited,a:hover{

    text-decoration: none;
}
//SPACING
.container{   
    max-width: 69.375rem;
    margin: 0 auto;    
    &--py{
        padding-top: 2.5rem;   
        padding-bottom: 2.5rem;        
    }
    &--px{
        padding-left: 1.5rem;        
        padding-right: 1.5rem;  
        padding-top: 0;      
        padding-bottom: 0;
    }
    &--pall{
    padding-top: 4.375rem;   
    padding-bottom :4.375rem; 
    padding-left: 1.5rem;        
    padding-right: 1.5rem;    
      }
    &--pt{
        padding-top: 2.5rem;  
        @include breakpoint-up(large){
            padding-top: 6rem;
        }   
      }
    &--pr{
        padding-right: 1.5rem;        
    }
    &--pb{
        padding-bottom: 2.5rem;    
        
        @include breakpoint-up(large){
            padding-bottom: 6rem;
        }    
    }
    &--pl{
        padding-left: 1.5rem;
    }
}

//Flex box styles 

.flex{
    display: flex;
    &-jc-sb{
        justify-content: space-between;
    }
    &-jc-c{
        justify-content: center;
    }
    &-ai-c{
        align-items: center;
    }
    &-jc-end{
        justify-content: end;
    }
}

//Button styless

button,.button{
    display: inline-block;
    padding: 0.875rem 2.1875rem;
    background-image: linear-gradient(to right,$limeGreen,$brightCyan);
    border-radius: 14px;
    border:none;
    cursor: pointer;
    color: $white;
    font-weight: 400;    
    font-size: $font-sm;

    &:hover{
        opacity: 0.7;
    }
}


.hide-for-mobile{
    @include breakpoint-down(medium){
        display: none;
    }
}

.hide-for-desktop{
    @include breakpoint-up(large){
        display: none;
    }
}
